.dashboard {
  background-color: #F2F2F2;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 2.8rem;
  text-align: center;
  font-weight: 700;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
}
.dashboard .formulario input[type=submit] {
  padding: 1rem;
  background-color: #385aca;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .dashboard .formulario input[type=submit] {
    width: auto;
  }
}
.dashboard .formulario input[type=submit]:hover {
  background-color: #2c47a3;
  cursor: pointer;
}
.dashboard .enalce {
  color: #385aca;
}
.dashboard .enalce:hover {
  color: #21367b;
}

.sidebar {
  opacity: 0;
  background-color: #385aca;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 300ms;
}
.sidebar.mostrar {
  padding: 2rem;
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #385aca;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
  font-size: 4rem;
}
.sidebar .contenedor-sidebar h2 .heading_sidebar {
  display: block;
  font-size: 3rem;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
  height: 2rem;
  width: 2rem;
}
.sidebar .contenedor-sidebar .cerrar-menu img {
  transition-property: transform;
  transition-duration: 300ms;
}
.sidebar .contenedor-sidebar .cerrar-menu img:hover {
  cursor: pointer;
  transform: scale(1.1);
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  text-transform: uppercase;
  font-size: 2.4rem;
  font-weight: bold;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #21367b;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    text-align: left;
  }
}
.sidebar .cerrar-sesion-mobile {
  margin-top: 10rem;
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion {
  background-color: #385aca;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion:hover {
  background-color: #2c47a3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile {
    display: none;
    align-items: center;
  }
}

.barra-mobile {
  background-color: #385aca;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 768px) {
