.work {
  max-height: 200rem;
}

.titulo {
  font-size: 3.5rem;
  color: #000;
  margin-top: 3rem;
}

.clientes_heading {
  text-align: center;
  font-size: 3.5rem;
  color: #000;
}

.cliente {
  border: 1px solid #e1e1e1;
  background-color: #FFFFFF;
  border-radius: 1rem;
  padding: 0 0 0 2rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
  height: 5rem;
}
.cliente_datos {
  display: flex;
  align-items: center;
  column-gap: 3rem;
}
.cliente:hover {
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
}
.cliente_nombre {
  color: #706b6d;
  font-size: 1.6rem;
  font-weight: 700;
  margin: 0;
  padding-right: 2rem;
  border-right: 2px solid #e1e1e1;
}
.cliente_razon {
  font-size: 1.2rem;
  font-weight: 400;
  margin: 0;
}
.cliente_botones {
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 0.3rem;
}

.informacion {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
}
.informacion_razon {
  border-top: 2px solid #e1e1e1;
  width: 30rem;
  padding-top: 3rem;
  padding-bottom: 2rem;
  font-weight: bold;
  justify-content: center;
}
.informacion_emails {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.informacion_email {
  width: 25rem;
  justify-content: space-between;
}
.informacion_contacto {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.informacion_tel {
  width: 25rem;
  justify-content: space-between;
}
.informacion_ubicacion {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.informacion_ubicacion_ubi {
  border: 1px solid #FFFFFF;
  border-bottom-left-radius: 2rem;
  border-top-left-radius: 2rem;
  padding: 0.6rem;
  padding-left: 1rem;
  min-width: 50rem;
  background-color: #FFFFFF;
}
.informacion_ubicacion_ubi:hover {
  box-shadow: 0.2rem 0.3rem 0.3rem #e1e1e1;
}
.informacion_ubicacion_ubica {
  width: 5rem;
  height: 5rem;
  border: 1px solid #FFFFFF;
  border-top-right-radius: 2rem;
